/* Navbar.css */
nav {
    background-color: #333;
    color: #fff;
    margin: 0; /* Remove margin to eliminate the gap at the top */
    padding: 0; /* Remove padding */
    padding-top: 15px;
    padding-bottom: 15px;
    font-size: 30px;
  }
  
  nav ul {
    list-style: none;
    padding: 0;
    display: flex;
    justify-content: center;
    margin: 0;
  }
  
  nav li {
    margin: 0 10px;
  }
  
  nav a {
    text-decoration: none;
    color: #fff;
  }
  
  nav a:hover {
    text-decoration: underline;
  }
  